'use client'
import React from 'react';
import Link from 'next/Link';
import { Posting } from '../types/job';
type Props = {
  job: Posting;
  index:number;
};

function CardComponent({ job ,index}: Props,) {
  
  return (
 
  <Link href={`/description/${index}`}>
    <h2>{job.title}</h2>
    <h4>
      {job.company}{' '}
      <ul className="list-disc p-4 m-0 inline-block">
        <li>{job.about.location} {job.id}</li>
      </ul>
    </h4>
    <p>{job.description}</p>
    <p>
      Posted on:{' '}
      {new Date(job.about.posted_on).toLocaleDateString('en-US', {
        year: 'numeric',
        month: 'long',
        day: 'numeric',
      })}
    </p>
  </Link>


  );
}

export default CardComponent;
